#home-container {
  width: 100%;
  height: 100vh;
  background: url("../assets/road.jpg");
  background-size: cover;

  display: flex;
  flex-direction: column;
  align-items: center;

  header {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px 0 5px;

    img {
      height: 50px;
    }

    ul {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      transition: 0.5s;
      cursor: pointer;
      list-style: none;

      li:hover {
        opacity: 0.8;
      }

      li {
        margin-left: 20px;
        a {
          color: #fff;
          font-family: "Ubuntu", sans-serif;
          font-size: 22px;
          text-decoration: none;
        }

        img {
          height: 25px;
        }
      }

      li.login-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3px 7px;
        background: #f46036;
        border-radius: 5px;
      }

      li.login-btn:hover {
        a {
          color: #f46036;
        }
        background: #fff;
        opacity: 1;
      }
    }
  }

  h1 {
    margin: 16% auto 1% auto;
    font-family: "Roboto", sans-serif;
    font-size: 40px;
    color: #fff;
  }

  #saiba {
    border: 3px solid #fff;
    padding: 10px;
    cursor: pointer;

    h2 {
      font-family: "Roboto", sans-serif;
      font-size: 30px;
      color: #fff;
    }
  }

  #saiba:hover {
    opacity: 0.8;
  }
}
